import { getOrderList, updateOrder } from '../../../api/index.js'
import { IMG_URL, goLoginPageTimeOut } from '../../../common/index.js'
export default{
	data(){
		return{
			IMG_URL:IMG_URL,
			orderState:[
				{state:3,label:"全部"},
				{state:0,label:"待发货"},
				{state:1,label:"待收货"},
				{state:2,label:"已完成"},
			],
			goodState:['待发货','已发货','已完成'],
			expressState:['提醒发货','确认收货','交易完成'],
			page:1,
			state:3,
			list:[],
			isLoading:false,
			showcfm:false,
			id:undefined,
			ending:true
		}
	},
	onLoad(option) {
		
	},
	onShow() {
		this.getOrders()
	},
	onReachBottom(){
		this.getOrders()
	},
	methods:{
		getOrders(){
			if(this.isBottom){
				this.isLoading = true
				setTimeout(()=>{
					this.isLoading = false
				},2000)
				return
			} 
			uni.showLoading({
				title: '加载中'
			})
			if(this.page==1) this.list = []
			let data = {
				page:this.page++,
				pageSize:10,
				state:this.state
			}
			getOrderList(data).then(res=>{
				this.isBottom = res.data.isLastPage
				this.list = this.list.concat(res.data.list)
				this.ending = this.list.length > 0
				uni.hideLoading()
			})
		},
		changeState(item){
			// 重置
			this.page=1
			this.state = item.state
			this.isBottom = false
			this.getOrders()
		},
		updateState(item){
			// 提醒发货
			if(item.state===0){
				uni.showToast({
				    title: '提醒发货成功',
				});
				return
			}
			// 确认完成
			if(item.state===1){
				this.showcfm = true
				this.id = item.id
			}
		},
		cfmOrder(){
			updateOrder({id:this.id,state:2}).then(res=>{
				this.showcfm = false
				this.changeState({state:this.state})
			})
		}
		
	}
}
